___CSS_LOADER_EXPORT___.push([module.id, `.menu-toggle {
    width: 40px;
    height: 40px;
    margin-top: calc(8vh - 20px);
    border: var(--clr-btn) 1px solid;
    border-radius: 50%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: sticky;
    top: calc(8vh - 20px);
    left: calc(100% - 8vh);
    
    translate: -50% 0;
    z-index: 20;
    pointer-events: all;
}

.bar {
    width: 16px;
    height: 1px;
    position: absolute;
    top: calc(50% + var(--i) * 4px);
    background-color: var(--clr-btn);
    transition: .5s ease-in;
    z-index: 5;
}

.menu-toggle.expand .bar {
    top: 50%;
    &:first-child {
        rotate: -225deg;
    }
    &:nth-child(2) {
        rotate: -135deg;
    }
    &:nth-child(3) {
        rotate: -45deg;
    }
    &:nth-child(4) {
        transition: .8s .4s;
        transform: 
            translateY(48px) 
            scaleX(4);
    }
    &:nth-child(5) {
        transition: .8s .4s;
        transform: 
            translateY(calc(var(--sectionNum) * 48px + 48px + 96px)) 
            scaleX(4);
    } 
}

.menu {
    position: fixed;
    top: 0;
    width: 11.6vh;
    height: 0;
    overflow: hidden;
    pointer-events: none;
    transition: .5s ease-in;
    transition-delay: 0s;
    
    .menu-toggle.expand & {
        height: calc(var(--sectionNum) * 48px + 48px + 96px);
        transition-delay: .25s !important;
    }
}

span {
    position: relative;
    right: 60px;
    color: var(--clr-btn);
    pointer-events: none;
    transition: 1s;
    
    .menu-toggle.expand & {
        right: 20px;
        opacity: 0;
    }
}


.to-top, 
.section-nav {
    display: block;
    width: 100%;
    height: 48px;
    text-align: center;
    color: var(--clr-btn);
    font-size: 1.2rem;
    pointer-events: all;
    &:hover {
        font-weight: 600;
        color: var(--clr-btn);
        text-decoration: none;
    }
}

.to-top ion-icon {
    translate: 0 25%;
}

.section-nav:first-child {
    margin-top: 96px;
}`, "",{"version":3,"sources":["webpack://./components/Menu.css"],"names":[],"mappings":"AAAA;IACI,WAAW;IACX,YAAY;IACZ,4BAA4B;IAC5B,gCAAgC;IAChC,kBAAkB;;IAElB,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;;IAEnB,gBAAgB;IAChB,qBAAqB;IACrB,sBAAsB;;IAEtB,iBAAiB;IACjB,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,WAAW;IACX,kBAAkB;IAClB,+BAA+B;IAC/B,gCAAgC;IAChC,uBAAuB;IACvB,UAAU;AACd;;AAEA;IACI,QAAQ;IACR;QACI,eAAe;IACnB;IACA;QACI,eAAe;IACnB;IACA;QACI,cAAc;IAClB;IACA;QACI,mBAAmB;QACnB;;qBAEa;IACjB;IACA;QACI,mBAAmB;QACnB;;qBAEa;IACjB;AACJ;;AAEA;IACI,eAAe;IACf,MAAM;IACN,aAAa;IACb,SAAS;IACT,gBAAgB;IAChB,oBAAoB;IACpB,uBAAuB;IACvB,oBAAoB;;IAEpB;QACI,oDAAoD;QACpD,iCAAiC;IACrC;AACJ;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,qBAAqB;IACrB,oBAAoB;IACpB,cAAc;;IAEd;QACI,WAAW;QACX,UAAU;IACd;AACJ;;;AAGA;;IAEI,cAAc;IACd,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,qBAAqB;IACrB,iBAAiB;IACjB,mBAAmB;IACnB;QACI,gBAAgB;QAChB,qBAAqB;QACrB,qBAAqB;IACzB;AACJ;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;AACpB","sourcesContent":[".menu-toggle {\n    width: 40px;\n    height: 40px;\n    margin-top: calc(8vh - 20px);\n    border: var(--clr-btn) 1px solid;\n    border-radius: 50%;\n\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n\n    position: sticky;\n    top: calc(8vh - 20px);\n    left: calc(100% - 8vh);\n    \n    translate: -50% 0;\n    z-index: 20;\n    pointer-events: all;\n}\n\n.bar {\n    width: 16px;\n    height: 1px;\n    position: absolute;\n    top: calc(50% + var(--i) * 4px);\n    background-color: var(--clr-btn);\n    transition: .5s ease-in;\n    z-index: 5;\n}\n\n.menu-toggle.expand .bar {\n    top: 50%;\n    &:first-child {\n        rotate: -225deg;\n    }\n    &:nth-child(2) {\n        rotate: -135deg;\n    }\n    &:nth-child(3) {\n        rotate: -45deg;\n    }\n    &:nth-child(4) {\n        transition: .8s .4s;\n        transform: \n            translateY(48px) \n            scaleX(4);\n    }\n    &:nth-child(5) {\n        transition: .8s .4s;\n  