    background-color: #232323;
    & ::-webkit-scrollbar {
        width: 0px;
    }
    font-family: 'Quicksand', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    scroll-behavior: smooth;
}

.sections {
    position: relative;
    z-index: 1;
}

.menu-container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    pointer-events: none;
}

footer {
    width: 100%;
    height: calc(16vh - 7px);
    background-color: #232323;
    text-align: center;
    
    & a {
        text-decoration: none;
        color: #fff;
        line-height: calc(16vh - 7px);
        &:hover {
            text-decoration: none;
            font-weight: 600;
            color: #fff;
        }
    }
}`, "",{"version":3,"sources":["webpack://./components/Main.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB;QACI,UAAU;IACd;IACA,gKAAgK;IAChK,uBAAuB;AAC3B;;AAEA;IACI,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,MAAM;IACN,oBAAoB;AACxB;;AAEA;IACI,WAAW;IACX,wBAAwB;IACxB,yBAAyB;IACzB,kBAAkB;;IAElB;QACI,qBAAqB;QACrB,WAAW;QACX,6BAA6B;QAC7B;YACI,qBAAqB;YACrB,gBAAgB;YAChB,WAAW;QACf;IACJ;AACJ","sourcesContent":["html {\n    background-color: #232323;\n    & ::-webkit-scrollbar {\n        width: 0px;\n    }\n    font-family: 'Quicksand', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n    scroll-behavior: smooth;\n}\n\n.sections {\n    position: relative;\n    z-index: 1;\n}\n\n.menu-container {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    pointer-events: none;\n}\n\nfooter {\n    width: 100%;\n    height: calc(16vh - 7px);\n    background-color: #232323;\n    text-align: center;\n    \n    & a {\n        text-decoration: none;\n        color: #fff;\n        line-height: calc(16vh - 7px);\n        &:hover {\n            text-decoration: none;\n            font-weight: 600;\n            color: #fff;\n        }\n    }\n}"],"sourceRoot":""}]);
