___CSS_LOADER_EXPORT___.push([module.id, `.box {
    position: absolute;
    top: calc(var(--z) * 1em);
    left: calc(2em * var(--x) - 1em);

    width: 2em;
    height: 2em;

    transform: translateZ(calc(2em * var(--y) - 6em));
    transform-style: preserve-3d;
    transition: .2s;

    & .top, 
    & .side, 
    & .bottom {
        position: absolute;
        
        width: 100%;
        height: 100%;
    }

    & .top {
        background-color: var(--clr-box-top);
        transform: rotateX(90deg) translateZ(1em);

        .pressed & {
            background-color: var(--clr-box-pressed);
        }
    }

    & .side {
        background-color: var(--clr-box-side);
        transform: 
            rotateY(calc(90deg * var(--i))) 
            translateZ(1em);
        pointer-events: none;

        &:nth-child(4),
        &:nth-child(5) {
            background-color: var(--clr-box-side-shadow);
        }
    }
}`, "",{"version":3,"sources":["webpack://./components/Box.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;IAClB,yBAAyB;IACzB,gCAAgC;;IAEhC,UAAU;IACV,WAAW;;IAEX,iDAAiD;IACjD,4BAA4B;IAC5B,eAAe;;IAEf;;;QAGI,kBAAkB;;QAElB,WAAW;QACX,YAAY;IAChB;;IAEA;QACI,oCAAoC;QACpC,yCAAyC;;QAEzC;YACI,wCAAwC;QAC5C;IACJ;;IAEA;QACI,qCAAqC;QACrC;;2BAEmB;QACnB,oBAAoB;;QAEpB;;YAEI,4CAA4C;QAChD;IACJ;AACJ","sourcesContent":[".box {\n    position: absolute;\n    top: calc(var(--z) * 1em);\n    left: calc(2em * var(--x) - 1em);\n\n    width: 2em;\n    height: 2em;\n\n    transform: translateZ(calc(2em * var(--y) - 6em));\n    transform-style: preserve-3d;\n    transition: .2s;\n\n    & .top, \n    & .side, \n    & .bottom {\n        position: absolute;\n        \n        width: 100%;\n        height: 100%;\n    }\n\n    & .top {\n        background-color: var(--clr-box-top);\n        transform: rotateX(90deg) translateZ(1em);\n\n        .pressed & {\n            background-color: var(--clr-box-pressed);\n        }\n    }\n\n    & .side {\n        background-color: var(--clr-box-side);\n        transform: \n            rotateY(calc(90deg * var(--i))) \n            translateZ(1em);\n        pointer-events: none;\n\n        &:nth-child(4),\n        &:nth-child(5) {\n            background-color: var(--clr-box-side-shadow);\n        }\n    }\n}"],"sourceRoot":""}]);
