___CSS_LOADER_EXPORT___.push([module.id, `.theme-switch {
    --width: calc(5.5rem);
    
    position: absolute;
    top: 50%;
    right: 0;
    
    width: var(--width);
    height: 55%;
    border-radius: 2rem 0 0 2rem;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    translate: calc(var(--width) - 3rem) -50%;
    transition: translate .2s ease-out;
    
    z-index: 20;

    opacity: 1;
    pointer-events: all;

    &.hide {
        pointer-events: none;
        & .indicator {
            opacity: 0;
        }
    }
    
    &:hover {
        translate: 0 -50%;
        border: solid 1px var(--clr-link-filler-border);
        border-right: none;
        background-color: var(--clr-link-filler);
        backdrop-filter: blur(1rem);

        & .indicator {
            translate: 3rem -50%;
            opacity: 0;
        }

        & input {
            translate: 0;
            opacity: 1;
        }
    }
    
    & .indicator {
        position: absolute;
        top: 50%;
        left: 100%;
    
        width: .3rem;
        height: 16rem;
    
        background-color: var(--clr-btn);
        border-radius: .5rem;
        opacity: .5;
    
        translate: -4rem -50%;
        transition: translate .1s, opacity .4s ease-in;

        display: flex;
        align-items: center;

        & i {
            color: var(--clr-btn);
            position: relative;
            font-size: 1.2rem;
            right: 2rem;
        }
    }

    & form {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;
    }

    & input {
        appearance: none;
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
        outline: var(--clr) .3rem solid;
        outline-offset: 2px;
        opacity: 0;
        transition: .1s;

        &:checked {
            background-color: var(--clr);
        }
    }
}`, "",{"version":3,"sources":["webpack://./components/ThemeSwitch.css"],"names":[],"mappings":"AAAA;IACI,qBAAqB;;IAErB,kBAAkB;IAClB,QAAQ;IACR,QAAQ;;IAER,mBAAmB;IACnB,WAAW;IACX,4BAA4B;;IAE5B,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;;IAEnB,yCAAyC;IACzC,kCAAkC;;IAElC,WAAW;;IAEX,UAAU;IACV,mBAAmB;;IAEnB;QACI,oBAAoB;QACpB;YACI,UAAU;QACd;IACJ;;IAEA;QACI,iBAAiB;QACjB,+CAA+C;QAC/C,kBAAkB;QAClB,wCAAwC;QACxC,2BAA2B;;QAE3B;YACI,oBAAoB;YACpB,UAAU;QACd;;QAEA;YACI,YAAY;YACZ,UAAU;QACd;IACJ;;IAEA;QACI,kBAAkB;QAClB,QAAQ;QACR,UAAU;;QAEV,YAAY;QACZ,aAAa;;QAEb,gCAAgC;QAChC,oBAAoB;QACpB,WAAW;;QAEX,qBAAqB;QACrB,8CAA8C;;QAE9C,aAAa;QACb,mBAAmB;;QAEnB;YACI,qBAAqB;YACrB,kBAAkB;YAClB,iBAAiB;YACjB,WAAW;QACf;IACJ;;IAEA;QACI,WAAW;QACX,YAAY;QACZ,aAAa;QACb,sBAAsB;QACtB,uBAAuB;QACvB,mBAAmB;QACnB,SAAS;IACb;;IAEA;QACI,gBAAgB;QAChB,WAAW;QACX,YAAY;QACZ,kBAAkB;QAClB,+BAA+B;QAC/B,mBAAmB;QACnB,UAAU;QACV,eAAe;;QAEf;YACI,4BAA4B;QAChC;IACJ;AACJ","sourcesContent":[".theme-switch {\n    --width: calc(5.5rem);\n    \n    position: absolute;\n    top: 50%;\n    right: 0;\n    \n    width: var(--width);\n    height: 55%;\n    border-radius: 2rem 0 0 2rem;\n    \n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    \n    translate: calc(var(--width) - 3rem) -50%;\n    transition: translate .2s ease-out;\n    \n    z-index: 20;\n\n    opacity: 1;\n    pointer-events: all;\n\n    &.hide {\n        pointer-events: none;\n        & .indicator {\n            opacity: 0;\n        }\n    }\n    \n    &:hover {\n        translate: 0 -50%;\n        border: solid 1px var(--clr-link-filler-border);\n        border-right: none;\n        background-color: var(--clr-link-filler);\n        backdrop-filter: blur(1rem);\n\n        & .indicator {\n            translate: 3rem -50%;\n            opacity: 0;\n        }\n\n        & input {\n            translate: 0;\n            opacity: 1;\n        }\n    }\n    \n    & .indicator {\n        position: absolute;\n        top: 50