___CSS_LOADER_EXPORT___.push([module.id, `.music-section {
    position: relative;
    width: 100%;
    height: 250vh;
    isolation: isolate;
    background: 
        linear-gradient(
            var(--clr-section-bg),
            var(--clr-box-top)
        );

    &.hide {
        display: none;
    }
}

.wrap {
    position: sticky;
    height: 40%;
    top: 0;

    display: flex;
    justify-content: center;
    align-items: center;
}

.mask {
    display: flex;
    justify-content: center;
    align-items: center;

    transition: .2s;
    overflow: hidden;
    
    width: max(560px, 30%);
    height: 480px;
}

iframe {
    width: max(560px, 30%);
    height: 90vh;
    top: -14.5px;
    position: relative;
}

.mask.wide {
    width: 960px;
    height: 394px;
}

.mask.wide iframe {
    width: 960px;
    scale: 1.06;
    translate: 0 80px;
}

.mask.normal {
    width: 838px;
    height: 472px;
    border-radius: 16px;
}

.mask.normal iframe {
    width: 960px;
    scale: 1.06;
    translate: 0 50px;
}

.text {
    color: #fff9;
    font-size: 4rem;
    width: 100%;
    text-align: center;
    font-weight: 800;

    position: absolute;
    top: 50%;
    z-index: -1;
}`, "",{"version":3,"sources":["webpack://./components/MusicSection.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;IAClB,WAAW;IACX,aAAa;IACb,kBAAkB;IAClB;;;;SAIK;;IAEL;QACI,aAAa;IACjB;AACJ;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,MAAM;;IAEN,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;;IAEnB,eAAe;IACf,gBAAgB;;IAEhB,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,sBAAsB;IACtB,YAAY;IACZ,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,WAAW;IACX,kBAAkB;IAClB,gBAAgB;;IAEhB,kBAAkB;IAClB,QAAQ;IACR,WAAW;AACf","sourcesContent":[".music-section {\n    position: relative;\n    width: 100%;\n    height: 250vh;\n    isolation: isolate;\n    background: \n        linear-gradient(\n            var(--clr-section-bg),\n            var(--clr-box-top)\n        );\n\n    &.hide {\n        display: none;\n    }\n}\n\n.wrap {\n    position: sticky;\n    height: 40%;\n    top: 0;\n\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.mask {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    transition: .2s;\n    overflow: hidden;\n    \n    width: max(560px, 30%);\n    height: 480px;\n}\n\niframe {\n    width: max(560px, 30%);\n    height: 90vh;\n    top: -14.5px;\n    position: relative;\n}\n\n.mask.wide {\n    width: 960px;\n    height: 394px;\n}\n\n.mask.wide iframe {\n    width: 960px;\n    scale: 1.06;\n    translate: 0 80px;\n}\n\n.mask.normal {\n    width: 838px;\n    height: 472px;\n    border-radius: 16px;\n}\n\n.mask.normal iframe {\n    width: 960px;\n    scale: 1.06;\n    translate: 0 50px;\n}\n\n.text {\n    color: #fff9;\n    font-size: 4rem;\n    width: 100%;\n    text-align: center;\n    font-weight: 800;\n\n    position: absolute;\n    top: 50%;\n    z-index: -1;\n}"],"sourceRoot":""}]);
